<template>
      <group class="jq-mt">
       <popup-picker :title="dateTile" :data="timeList" :value.sync="time" ></popup-picker>
      </group>
      <!--销售仪表盘-->
      <group class="jq-mt" @click='clickSales'>
        <div class="panLittle" v-egauge="gaugeSalesData"></div>
      </group>

      <group class="jq-mt" @click='clickCost'>
      <div class="panLittle" v-egauge="gaugeContData"></div>
      </group>

      <!--利润仪表盘-->
      <group class="jq-mt" @click='clickProfit'>
      <div class="panLittle" v-egauge="gaugeProfitData"></div>
      </group>

</template>
<script>
  //import { Group,FlexboxItem, Flexbox,PopupPicker } from 'vux/src/components'
  import {Flexbox,FlexboxItem} from '*/flexbox'
  import PopupPicker from '*/popup-picker'
  import Group from '*/group'

  import { getList } from '../../../untils/api'
  export default{
     components: {
      PopupPicker,
      Group
    },
    data () {
      return {
        dateTile:'日期',
        time: formatData.defaultTime,
        timeList: formatData.dateList,
        permissions:localStorage.getItem("permissions"),
        gaugeSalesData:{},
        gaugeContData:{},
        gaugeProfitData:{}
      }
    },

    ready () {
     document.title = '销售管理驾驶舱';
    },
      methods: {
          clickSales(){
            if(this.permissions=='202'){
               this.$route.router.go({name: 'salesmap'});
            }else{
              this.$route.router.go({name: 'salesmapsa'});
            }
          },
          clickCost(){
            this.$route.router.go({name: 'cbsa'});
          },
          clickProfit(){
             this.$route.router.go({name: 'profitSA'});
          },

        async GetSalesData() {
            commit('UPDATE_LOADING', true)
            let id = "";
             if(this.permissions=='201'){//fpi
               id ='APP10131a';
             }else if(this.permissions=='202'){
               id ='APP10131c';
             }else if(this.permissions=='203'){
              id ='APP10131b';
             }
            let date=formatData.dateConversion(this.time);
            let dataTit = await getList(id,{date_:date});
            //console.log(dataTit);
          if(dataTit.success){
            const TableList = dataTit.data;
            this.gaugeSalesData = {};
            this.gaugeSalesData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
              commit('UPDATE_LOADING', false)
          }else{
              commit('UPDATE_LOADING', false)
            console.log(dataTit.message);
          }
          let gaugeSalesData = this.gaugeSalesData
        },
        async GetContData() {
            commit('UPDATE_LOADING', true)
            let id = "";
             if(this.permissions=='201'){//fpi
               id ='APP10132a';
             }else if(this.permissions=='202'){
               id ='APP10132c';
             }else if(this.permissions=='203'){
              id ='APP10132b';
             }

            let date=formatData.dateConversion(this.time);
            let dataTit = await getList(id,{date_:date});
            //console.log(dataTit);
          if(dataTit.success){
            const TableList = dataTit.data;
            this.gaugeContData = {};
            this.gaugeContData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
              commit('UPDATE_LOADING', false)
          }else{
              commit('UPDATE_LOADING', false)
            console.log(dataTit.message);
          }
          let gaugeContData = this.gaugeContData
        },
        async GetProfitData() {
            commit('UPDATE_LOADING', true)
            let id = "";
             if(this.permissions=='201'){//fpi
               id ='APP10133a';
             }else if(this.permissions=='202'){
               id ='APP10133c';
             }else if(this.permissions=='203'){
              id ='APP10133b';
             }

            let date=formatData.dateConversion(this.time);
            let dataTit = await getList(id,{date_:date});
            //console.log(dataTit);
          if(dataTit.success){
            const TableList = dataTit.data;
            this.gaugeProfitData = {};
            this.gaugeProfitData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
              commit('UPDATE_LOADING', false)
          }else{
              commit('UPDATE_LOADING', false)
            console.log(dataTit.message);
          }
          let gaugeProfitData = this.gaugeProfitData
        },

    },
    watch: {
     time (newtime, oldtime) {
      this.GetSalesData();
      this.GetContData();
      this.GetProfitData();
       formatData.setDateTime(newtime);
     }
    },
    created: function () {
      this.gaugeSalesData = {};
      this.gaugeSalesData.dataset = [];
      this.GetSalesData();

      this.gaugeContData = {};
      this.gaugeContData.dataset = [];
      this.GetContData();

      this.gaugeProfitData = {};
      this.gaugeProfitData.dataset = [];
      this.GetProfitData();
    }
  }
</script>



